import React, { useState, useEffect } from "react";
import { createCart, getProductCartStatus } from "../../library/parse";
import NumberTextInput from "../CRUDForm/NumberTextInput";
import Button from "../../components/Button";
import Parse from "../../library/parse";
import { Box } from "grommet";

const Component = ({ product, enquiryComponent }) => {

  const [bidAmount, setBidAmount] = useState(1)
  const [productStatus, setProductStatus] = useState([]);
  const user = Parse.User.current();
  const displayPrice =
    product.get("closePrice") && product.get("closePrice").toFixed(2);
  const [qty, setQty] = useState(0);
  const loadProductCart = () => {
    return getProductCartStatus(product).then((e) => {
      const initQty = product.get("quantity");
      if (e.length > 0) {
        setProductStatus(e);
        setQty(initQty - e[0].get("quantity"));
      } else {
        setQty(initQty);
      }
    });
  };
  const [buyQuantitiy, setBuyQuantity] = useState(1);

  useEffect(() => {
    loadProductCart();
  }, []);

  return (
    <>
      <Box margin="0">{`$${displayPrice} Left: ${qty}`}</Box>

      {buyQuantitiy > 1 && (
        <NumberTextInput
          size="small"
          placeholder="buy how many?"
          onChange={(v) => setBuyQuantity(v)}
        />
      )}
      <Box justify="center" alignContent="center"  direction="row" gap="small">
        <Box width={{ max: "40%"}}>
        <NumberTextInput 
          value={bidAmount}
          size="small"
          onChange={v => setBidAmount(v)}
        />
        </Box>
        <Button
          focusIndicator
          onClick={() => {
            if (user) {
              return createCart(
                product.id,
                bidAmount,
                1
              ).then(() => loadProductCart());
            }
          }}
          label={productStatus.length ? "Awarded" : "Bid"}
          disabled={productStatus.length}
          size="small"
          primary
        />
      </Box>
    </>
  );
};

export default Component;
