import React, { useState, useEffect } from "react";
import {
  Button,
  Tabs,
  Tab,
  Box,
  Anchor,
  MaskedInput,
  ResponsiveContext,
} from "grommet";
import CardListing from "../../components/CardList";
import { Cart } from "grommet-icons";
import CRUDForm from "../CRUDForm";
import ProductFormConfig from "./ProductFormConfig";
import Modal from "../../components/Modal";
import { crud, searchProduct, search } from "../../library/parse";
import EventObserver from "../EventObserver";
import SellingMode from "./SellingMode";
import OrderListing from "../Order/OrderListing";
import { encodeURIComponent } from "../../helpers";
import { createObject } from "../../library/parse";
import ProductUploader from "./ProductUploader";

const Component = ({ storeId, editMode, onClickAppsIcon }) => {
  const size = React.useContext(ResponsiveContext);
  const [uploadMultipleMode, setUploadMultipleMode] = useState(false);
  const [cudMode, setCUDMode] = useState("");
  const [activeModel, setActiveModel] = useState({});
  const [showActionModal, setShowActionModal] = useState(false);
  const [event, setEvent] = useState(null);
  const [
    scheduledForFetchingProduct,
    setScheduledForFetchingProduct,
  ] = useState(false);
  const [products, setProducts] = useState([]);

  useEffect(() => {
    const loadProducts = () => {
      searchProduct({
        storeId: {
          f: "equalTo",
          v: storeId,
        },
        limit: 10,
        descending: "createdAt",
      }).then((e) => setProducts(e));
    };
    loadProducts(storeId);
  }, [storeId, event]);

  return (
    <Box>
      {editMode && (
        <Box fill={true} gap="small" direction="row" align="end">
          <Anchor onClick={() => setCUDMode("create")}>Add Product</Anchor>
          <Anchor onClick={() => setUploadMultipleMode(true)}>
            Upload Multiple Product
          </Anchor>
        </Box>
      )}
      <CardListing
        showFallBack={true}
        onClickAppsIcon={() => setShowActionModal(true)}
        dataList={products.map((e) => {
          return {
            title: e.get("name"),
            thumbnail: e.get("photos"),
            cta: (
              <>
                <SellingMode product={e} />
                {editMode && (
                  <Button
                    onClick={() => {
                      setActiveModel(e.toJSON());
                      setCUDMode("update");
                    }}
                    size="small"
                    margin="small"
                    primary
                    label="Edit"
                  />
                )}
              </>
            ),
          };
        })}
      />
      {uploadMultipleMode && (
        <Modal
          full={size === "small"}
          onClose={() => setUploadMultipleMode(false)}
        >
          <ProductUploader storeId={storeId} />
        </Modal>
      )}
      {cudMode && (
        <Modal full={size === "small"} onClose={() => setCUDMode("")}>
          <Tabs>
            <Tab title="Information">
              <CRUDForm
                onClick={(action, model) => {
                  crud(
                    "Product",
                    {
                      ...model,
                      available: true,
                    },
                    action,
                    "",
                    {
                      storeId: storeId,
                    }
                  );
                  setCUDMode("");
                }}
                mode={cudMode}
                model={activeModel}
                formConfig={ProductFormConfig}
                objectClass="Product"
              />
            </Tab>
            <Tab title={<Cart />}>
              <OrderListing product={activeModel} />
            </Tab>
          </Tabs>
        </Modal>
      )}
      <EventObserver
        objectClass={"Product"}
        onEvent={(e, action) => {
          if (!scheduledForFetchingProduct) {
            setTimeout(() => {
              setEvent(e);
              setScheduledForFetchingProduct(false);
            }, 5000);
            setScheduledForFetchingProduct(true);
          }
        }}
      />
      {showActionModal && <Modal>Test</Modal>}
    </Box>
  );
};

export default Component;
