import React, { useState, useEffect } from "react";
import InstantClose from "./InstantClose";
import Auction from './Auction'
const Component = ({ product }) => {
  const [sellingMode, setSellingMode] = useState(product.get("sellingMode"));
  return (
    <>
    {sellingMode === "InstantClose" && <InstantClose product={product} />}
    {sellingMode === "Auction" && <Auction product={product}/>}
    </>
  );
};

export default Component;
