import React, { useState } from "react";
import Box from "../../components/Box";
import DropZone from "../../components/DropZone";
import { createObject } from "../../library/parse";
import NumberTextInput from "../CRUDForm/NumberTextInput";

const Component = ({ storeId }) => {
  const [price, setPrice] = useState(5);

  return (
    <Box>
      <NumberTextInput value={price} onChange={(v) => setPrice(v)} />
      <DropZone
        onChange={(files) => {
          files.forEach((file) => {
            createObject(
              "Product",
              {
                name: file._metadata.name.replace(/\.[^/.]+$/, ""),
                startingPrice: price,
                closePrice: price,
                photos: [file],
                quantity: 1,
              },
              {
                storeId,
              }
            );
          });
        }}
      />
    </Box>
  );
};

export default Component;
